<template>
  <div class="nav">
    <div class="logo">
      <img alt="Vue logo" width="40" height="40" src="../assets/logo.png" />
      <span>YFIL</span>
    </div>
    <div class="menu">
      <el-menu
        class="el-menu-demo"
        mode="horizontal"
      >
        <el-menu-item index="1">{{ $t("airdrop") }}</el-menu-item>
        <el-menu-item index="2">{{ $t("twitter") }}</el-menu-item>
        <el-menu-item index="3">{{ $t("gitHub") }}</el-menu-item>
      </el-menu>
    </div>
    <div class="dropdown">
      <el-dropdown trigger="click" @command="handleCommand">
        <span class="el-dropdown-link">
          {{langu}}<el-icon class="el-icon--right"><arrow-down /></el-icon>
        </span>
        <template #dropdown>
          <el-dropdown-menu>
            <el-dropdown-item command="en">English</el-dropdown-item>
            <el-dropdown-item command="zhCN"></el-dropdown-item>
            <!-- <el-dropdown-item command="zhCT"></el-dropdown-item> -->
          </el-dropdown-menu>
        </template>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  data() {
    return {
      
    };
  },
  mounted() {
    
  },
  methods:{
    handleCommand(a){
      this.$i18n.locale = a;
      localStorage.setItem("lang", a);
    }
  },
  computed:{
    langu(){
      let lang = ''
      switch (this.$i18n.locale) {
        case 'zhCN':
          lang = ''
          break;
        case 'zhCT':
          lang = ''
          break;
        case 'en':
          lang = 'English'
          break;
      
        default:
          lang = ''
          break;
      }
      return lang
    }
  }
};

</script>

<style scoped>
.nav {
  display: flex;
}
.logo{
  display: flex;
  align-items: center;
  margin-right: 100px;
}
.logo img{
  border-radius: 50%;
  border: 1px solid #409eff;
  margin-right: 10px;
  padding: 5px;
}
.logo span{
  font-size: 25px;
  color: #409eff;
}
.nav > .menu {
  flex: 1;
}
.dropdown{
  display: flex;
  align-items: center;
}
</style>
<style>

.el-menu--horizontal{
  border: none;
  background: transparent;
}
.el-menu--horizontal>.el-menu-item{
  color: #f9f9f9;
}
.el-dropdown{
  color: #f9f9f9;
}

</style>



