<template>
  <div>
    
  <el-carousel
    height="100vh"
    trigger="click"
    :loop="false"
    direction="vertical"
    :autoplay="false"
    @mousewheel.native="rollScroll"
    @mousedown.native="mousedown"
    @mousemove.native="mousemove"
    @mouseup.native="mouseup"
    ref="carouselRef"
  >
    <el-carousel-item>
      <div class="body">
        <div class="navbar">
          <navbar></navbar>
        </div>
        <div class="content">
          <div class="title">YFILChain</div>
          <div>
            {{$t("index.content")}}
          </div>
        </div>
        <div class="btn" @click="goClick()">{{$t("index.goair")}}</div>
          <div class="bian_img" style="text-align: center;margin-top: 20px;">
            <!-- <img src="../assets/img/7.png" alt=""> -->
          </div>
      </div>
    </el-carousel-item>
    <el-carousel-item>
      <div class="step2">
        <div class="con">
          <div class="box">
            <div class="boxitem">
              <div class="boxitem_img img1"></div>
              <div class="boxitem_title">{{$t("index.step2title1")}}</div>
              <p style="position:relative;"><span style="color:#4d85fa;font-size: 40px;position: absolute;left:-15px;top: 0;"></span>{{$t("index.step2title1_1")}}</p>
              <p style="position:relative;"><span style="color:#4d85fa;font-size: 40px;position: absolute;left:-15px;top: 0;"></span>{{$t("index.step2title1_2")}}</p>
            </div>
            <div class="boxitem">
              <div class="boxitem_img img2"></div>
              <div class="boxitem_title">{{$t("index.step2title2")}}</div>
              <p style="position:relative;"><span style="color:#4d85fa;font-size: 40px;position: absolute;left:-15px;top: 0;"></span>{{$t("index.step2title2_1")}}</p>
              <p style="position:relative;"><span style="color:#4d85fa;font-size: 40px;position: absolute;left:-15px;top: 0;"></span>{{$t("index.step2title2_2")}}</p>
              <p style="position:relative;"><span style="color:#4d85fa;font-size: 40px;position: absolute;left:-15px;top: 0;"></span>{{$t("index.step2title2_3")}}</p>

            </div>
            <div class="boxitem">
              <div class="boxitem_img img3"></div>
              <div class="boxitem_title">{{$t("index.step2title3")}}</div>
              <p style="position:relative;"><span style="color:#4d85fa;font-size: 40px;position: absolute;left:-15px;top: 0;"></span>{{$t("index.step2title3_1")}}</p>
              <p style="position:relative;"><span style="color:#4d85fa;font-size: 40px;position: absolute;left:-15px;top: 0;"></span>{{$t("index.step2title3_2")}}</p>
              <p style="position:relative;"><span style="color:#4d85fa;font-size: 40px;position: absolute;left:-15px;top: 0;"></span>{{$t("index.step2title3_3")}}</p>
              <p style="position:relative;"><span style="color:#4d85fa;font-size: 40px;position: absolute;left:-15px;top: 0;"></span>{{$t("index.step2title3_4")}}</p>

            </div>
          </div>
          <div class="address">
            <div class="title">{{$t("index.step2title4")}}<span></span></div>
            <div class="change">
              0x482024eDFEeE697fC0555FD31665C152a665bBA6
            </div>
            <div class="oxchange">
              <input type="text" v-model="state.money" />
              <div class="btn" @click="changeEth">{{$t("index.step2title4btn")}}</div>
            </div>
          </div>
        </div>
      </div>
    </el-carousel-item>
    <el-carousel-item>
      <div class="step3">
        <!-- <div>
          <el-button type="primary" @click="btnclick">{{
            state.isconnect ? "" : ""
          }}</el-button>
          <button @click="getBalance">{{ state.balance }}</button>
        </div>
        <div>
          {{ state.num }}
          <button @click="updateBtn">+1</button>
        </div> -->
        <div class="top">
          <div>
            <div class="title">
            {{$t("index.step3title1")}}
          </div>
          <p>
            <div class="div img1"></div>
            <span>{{$t("index.step3title2")}}</span>
          </p>
          <p>
            <div class="div img2"></div>
            <span>{{$t("index.step3title3")}}</span>
          </p>
          <p>
            <div class="div img3"></div>
            <span>{{$t("index.step3title4")}}</span>
          </p>
          </div>
          <div class="big_img">
            <img src="../assets/img/car.png" alt="">
          </div>
        </div>
        <div class="content">
          <div class="title">{{$t("index.step3title5")}}</div>
          <div class="tips">{{$t("index.step3title6")}}</div>
          <div class="time">
            <span class="span">{{ state.timeDate.d }}</span
            ><span style="margin: 0 10px">d</span>
            <span class="span">{{ state.timeDate.h }}</span
            ><span style="margin: 0 10px">h</span>
            <span class="span">{{ state.timeDate.m }}</span
            ><span style="margin: 0 10px">m</span>
            <span class="span">{{ state.timeDate.s }}</span
            ><span style="margin: 0 10px">s</span>
          </div>
          <div class="ox">
            <input type="text" v-model="state.address" @input="balanceChange"/>
            <el-button class="btn" @click="getToken" :loading="lock">{{lock?'':''}}</el-button>
            <div class="osx">
              {{$t("index.step3title7")}}: {{ state.balance }} eth
            </div>
          </div>
        </div>
      </div>
    </el-carousel-item>
  </el-carousel>
  </div>
</template>

<script>
import navbar from "../components/navbar.vue";
import MyToken from "../json/MyToken.json";
import Box1 from "../json/Box1.json";
const Web3 = require("web3");
const Tx = require("ethereumjs-tx").Transaction;
// const web3 = new Web3(new Web3.providers.HttpProvider("http://127.0.0.1:7545"));
const infura_apikey = '542ee2b036fa4058838a5bde0bb623a5'//PROJECT ID
const web3 = new Web3(new Web3.providers.HttpProvider(`https://ropsten.infura.io/v3/${infura_apikey}`));
console.log(ethereum); // metamask
const privateKey1 = Buffer.from(process.env.VUE_APP_KEYDU, "hex");
export default {
  name: 'App',
  components:{
    navbar
  },
  data(){
    return{
      myContract: {},
      myBox1: {},
      state: {
        address: '',
        isconnect: false,
        balance: 0,
        num: 0,
        timeNum: 0,
        mouse: {
          isclick: false,
          y: 0,
          time:0
        },
        timeDate: {
          d: 0,
          h: 0,
          m: 0,
          s: 0,
        },
      },
      lock: false
    }
  },
  mounted(){
    this.myContract = new web3.eth.Contract(
      MyToken.abi,
      "0x482024eDFEeE697fC0555FD31665C152a665bBA6"
    );
    console.log(this.myContract);
    // this.myBox1 = new web3.eth.Contract(
    //   Box1.abi,
    //   "0xddB8605E8E43E00218cE6A18c0502FE4437DAf2b"
    // );
    // this.myBox1.methods
    //   .retrieve()
    //   .call()
    //   .then((res) => {
    //     console.log(res);
    //     state.num = res;
    //   });
    setInterval(() => {
      this.getTimes("2022-10-24 23:59:59");
    }, 1000);
  },
  methods:{
    updateBtn(){
      this.myBox1.methods
        .increment()
        .send({ from: this.state.address })
        .then((res) => {
          console.log(res);
          this.state.num++;
        });
    },
    async requestAcc(){
      return new Promise((resolve, reject) => {
        ethereum
          .request({ method: "eth_requestAccounts" })
          .then((res) => {
            resolve(res);
          })
          .catch((err) => {
            reject(err);
          });
      });
    },
    async btnclick(){
      this.requestAcc()
        .then((res) => {
          if (res) {
            this.state.address = res[0];
            this.state.isconnect = true;
            // getBalance();
          }
        })
        .catch((err) => {
          alert("");
        });
    },
    getToken(){
      if(web3.utils.isAddress(this.state.address)==false){
        this.alert("");
        return
      }
      try {
        this.lock = true;
  
        this.myContract.methods.isAirDrop(this.state.address).call().then((res) => {
          console.log(this.myContract.methods);
          if (res) {
            this.alert("");
            this.lock = false
            return;
          }
          //
          web3.eth.getTransactionCount('0xBBD8d8932350dA69b90492bf414447481A543481', (err, txCount) => {
            const txObject = {
              nonce:    web3.utils.toHex(txCount),
              gasLimit: web3.utils.toHex(8000000),
              gasPrice: web3.utils.toHex(web3.utils.toWei('10', 'gwei')),
              to: '0x482024eDFEeE697fC0555FD31665C152a665bBA6',
              data: this.myContract.methods.changeAirDrop(this.state.address).encodeABI()
            }
            // 
            const tx = new Tx(txObject, { chain: 'ropsten', hardfork: 'petersburg' })
            tx.sign(privateKey1)

            const serializedTx = tx.serialize()
            const raw = '0x' + serializedTx.toString('hex')

            // 
            web3.eth.sendSignedTransaction(raw, (err, txHash) => {
              console.log('txHash:', txHash)
              this.lock = false
              if (!txHash) {
                this.alert("");
                return
              }
              this.alert("");
              // times = setInterval(() => {
              //   web3.eth.getTransactionReceipt(txHash).then(res=>{
              //       // console.log(res);
              //     if (!res) {
              //       this.lock = true
              //     }else{
              //       clearInterval(times)
              //       this.lock = false
              //       if (res.status) {
              //         this.alert("");
              //       }else{
              //         this.alert("");
              //       }
              //     }
              //   });
              // }, 1000);
            })
          })
        });
        
        // this.myContract.methods
        //   .changeAirDrop(this.state.address)
        //   .send({ from: this.state.address }) // send gas
        //   .then((res) => {
        //     console.log(res);
        //     alert("");
        //     getBalance();
        //   })
        //   .catch((error) => {
        //     console.log(error);
        //     alert("");
        //   });
      } catch (error) {
        alert("");
      }
    },
    getBalance(){
      this.myContract.methods
        .getBalance(this.state.address)
        .call() // call  gas
        .then((res) => {
          this.state.balance = res / 10 ** 18;
          console.log(res);
        });
    },
    balanceChange(val){
      if(web3.utils.isAddress(this.state.address)){
        this.getBalance(this.state.address)
      }
    },
    changeEth(){
      if (isNaN(this.state.money)) {
        alert("");
        return;
      }
      this.requestAcc()
        .then((res) => {
          if (res) {
            ethereum
              .request({
                method: "eth_sendTransaction",
                params: [
                  {
                    from: res[0],
                    to: "0x482024eDFEeE697fC0555FD31665C152a665bBA6",
                    value: web3.utils.toHex(this.state.money * 10 ** 18),
                  },
                ],
              })
              .then((res) => {
                console.log(res);
                this.state.money = 0;
              })
              .catch((err) => {
                alert("");
              });
          }
        })
        .catch((err) => {
          alert("");
        });
    },
    rollScroll(event){
      let scrollVal = event.wheelDelta || event.detail;
      if (this.state.timeNum) {
        clearTimeout(this.state.timeNum);
      }
      this.state.timeNum = setTimeout(() => {
        scrollVal > 0 ? this.$refs.carouselRef.prev() : this.$refs.carouselRef.next();
      }, 300);
    },
    mousedown(e){
      this.state.mouse.isclick = true;
      this.state.mouse.y = e.clientY;
      this.state.mouse.time = new Date().getTime()
    },
    mousemove(e){
      // if (this.state.mouse.isclick) {
      //   e.clientY - this.state.mouse.y > 0
      //     ? this.$refs.carouselRef.prev()
      //     : this.$refs.carouselRef.next();

      //   this.state.mouse.isclick = false;
      // }
    },

    mouseup(e){
      if (new Date().getTime() - this.state.mouse.time < 1000 && Math.abs(e.clientY - this.state.mouse.y)>50) {
        if (this.state.mouse.isclick) {
          e.clientY - this.state.mouse.y > 0
            ? this.$refs.carouselRef.prev()
            : this.$refs.carouselRef.next();
        }
      }
      this.state.mouse.isclick = false;
    },

    getTimes(data){
      let time = parseInt((new Date(data).getTime() - new Date().getTime()) / 1000);
      this.state.timeDate.d =
        parseInt(time / 86400) > 9
          ? parseInt(time / 86400)
          : "0" + parseInt(time / 86400);
      this.state.timeDate.h =
        parseInt((time % 86400) / 3600) > 9
          ? parseInt((time % 86400) / 3600)
          : "0" + parseInt((time % 86400) / 3600);
      this.state.timeDate.m =
        parseInt(((time % 86400) % 3600) / 60) > 9
          ? parseInt(((time % 86400) % 3600) / 60)
          : "0" + parseInt(((time % 86400) % 3600) / 60);
      this.state.timeDate.s =
        parseInt(((time % 86400) % 3600) % 60) > 9
          ? parseInt(((time % 86400) % 3600) % 60)
          : "0" + parseInt(((time % 86400) % 3600) % 60);
    },
    goClick(){
      this.$refs.carouselRef.next();
      setTimeout(() => {
        this.$refs.carouselRef.next();
      }, 300);
    },
    alert(msg, btn=''){
      this.$alert(msg, {
        confirmButtonText: btn,
        showClose: false,
        closeOnClickModal: true
      }).then(()=>{}).catch(()=>{})
    },
  }
}
</script>
<style lang="scss" scoped>
*{
  letter-spacing: .12em;
}
a {
  color: #42b983;
}
input:focus-visible {
  border: none;
}
.body {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(#3136d1, #5082f0);
  .btn {
    width: 150px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    margin: 0 auto;
    color: #141aba;
    background: #00AFE8;
    border-radius: 10px;
    cursor: pointer;
  }
  .content {
    width: 800px;
    text-align: center;
    margin: 100px auto 20px;
    color: #f9f9f9;
    line-height: 27px;
    .title {
      margin: 20px auto;
      font-size: 30px;
      font-weight: bold;
    }
  }
  
  .bian_img{
    width: 100%;
    height: 480px;
    margin:0 auto;
    background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(75, 122, 244, 1)), url(../assets/img/7.png);
    background-position:center;
    background-repeat: no-repeat;
  }
}
.navbar {
  width: 1200px;
  margin: 0 auto;
}

.step2 {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(#c8e6fa, #d5e6fd);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .con {
    width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
    .box {
      display: flex;
      justify-content: space-between;
      .boxitem {
        width: 350px;
        box-sizing: border-box;
        padding: 20px 30px 30px;
        background: #fff;
        border-radius: 8px;
        line-height: 26px;
        .boxitem_img {
          width: 80px;
          height: 80px;
          background-size: 100% 100%;
          background-repeat: no-repeat;
          margin-bottom: 10px;
        }
        .boxitem_img.img1 {
          background-image: url(../assets/img/1.png);
        }
        .boxitem_img.img2 {
          background-image: url(../assets/img/2.png);
        }
        .boxitem_img.img3 {
          background-image: url(../assets/img/3.png);
        }
        .boxitem_title {
          font-size: 20px;
          font-weight: 600;
          color: #4d85fa;
          margin-bottom: 10px;
        }
      }
    }
    .address {
      .title {
        font-size: 25px;
        font-weight: 600;
        color: #4d85fa;
        margin: 80px auto 10px;
        text-align: center;
      }
      .oxchange {
        display: flex;
        width: 500px;
        margin: 0 auto;
        height: 46px;
        align-items: center;
        background: #f9f9f9;
        box-shadow: 0 0 15px 5px #7295e0;
        border-radius: 30px;
        margin: 0 auto;
        text-align: center;
        input {
          border: none;
          outline: none;
          flex: 1;
          margin-left: 50px;
          text-align: center;
          background: transparent;
        }
        .btn {
          width: 80px;
          height: 40px;
          line-height: 40px;
          text-align: center;
          color: #fff;
          background: #0e77fa;
          border-radius: 20px;
          margin-right: 3px;
          cursor: pointer;
        }
      }
      .change {
        font-size: 16px;
        font-weight: 600;
        text-align: center;
        color: #333;
        margin: 10px auto 20px;
        letter-spacing: 0;
      }
    }
  }
  .btn {
    width: 150px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    margin: 0 auto;
    color: #141aba;
    background: #00ccff;
    border-radius: 10px;
    cursor: pointer;
  }
}
.step3 {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(#f5f6fa, #d5e6fd);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .top {
    width: 1200px;
    padding: 70px 30px;
    box-sizing: border-box;
    background: #f9f9f9;
    margin: 0 auto;
    display: flex;
    .big_img{
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .title {
      width: 600px;
      font-size: 25px;
      font-weight: bold;
    }
    p {
      width: 600px;
      display: flex;
      span{
        flex: 1;
      }
      .div {
        width: 50px;
        height: 50px;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        margin-bottom: 10px;
        margin-right: 10px;
      }
      .img1{
        background-image: url(../assets/img/4.png);
      }
      .img2{
        background-image: url(../assets/img/5.png);
      }
      .img3{
        background-image: url(../assets/img/6.png);
      }
    }
  }
  .content {
    width: 1200px;
    background-image: linear-gradient(#2329dd, #05bafa);
    text-align: center;
    color: #f9f9f9;
    padding: 10px 0 30px;
    margin: 0 auto;
    .title {
      font-size: 28px;
      font-weight: 600;
      margin: 20px auto;
    }
    .tips {
      font-size: 22px;
      font-weight: 500;
      margin: 20px auto 20px;
      color: #f8f8f8;
    }
    .time {
      font-size: 25px;
      font-weight: 500;
      margin: 10px auto;
      color: #f9f9f9;
      .span {
        color: #55D9A8;
      }
    }
    .ox {
      display: flex;
      width: 500px;
      height: 46px;
      align-items: center;
      background: #f9f9f9;
      box-shadow: 0 0 15px 5px #7295e0;
      border-radius: 30px;
      margin: 0 auto;
      text-align: center;
      position: relative;
      input {
        border: none;
        outline: none;
        flex: 1;
        margin-left: 50px;
        text-align: center;
        background: transparent;
      }
      .btn {
        padding: 0;
        width: 80px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: #fff;
        background: #0e77fa;
        border-radius: 20px;
        margin-right: 3px;
        cursor: pointer;
      }
      .osx{
        position: absolute;
        bottom: -25px;
        left: 15px;
        font-size: 14px;
      }
    }
  }
}
</style>
<style>
.el-menu--horizontal .el-menu-item:not(.is-disabled):hover{
  background: transparent !important;
}
.el-menu--horizontal>.el-menu-item.is-active{
  background: transparent !important;
  color: #00afe8 !important;
  border: none !important;
}
.el-menu.el-menu--horizontal{
  border: none !important;
  background: transparent !important;
}
.el-menu--horizontal .el-menu-item:not(.is-disabled):hover{
  background: transparent !important;
  color: #00afe8 !important;
}
.el-menu--horizontal>.el-menu-item{
  color: #FFF !important;
}
</style>



