<template>
  <div>
    <navbar></navbar>
    <div>
      <el-button type="primary">{{
        state.isconnect ? "" : ""
      }}</el-button>
    </div>
    <div>
      <input type="text" v-model="state.address" />
      <button>{{ $t("airdrop") }}</button>
      <button>{{ state.balance }}</button>
    </div>
    <div>
      <input type="text" v-model="state.money" />
      <button></button>
    </div>
    <el-carousel height="200px" direction="vertical" :autoplay="false">
      <el-carousel-item v-for="item in 3" :key="item">
        <h3 class="medium">{{ item }}</h3>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import navbar from "../components/navbar.vue";
import MyToken from "../json/MyToken.json";
const Web3 = require("web3");
const Tx = require("ethereumjs-tx").Transaction;
const infura_apikey = "542ee2b036fa4058838a5bde0bb623a5"; //PROJECT ID
const web3 = new Web3(
  new Web3.providers.HttpProvider(
    `https://ropsten.infura.io/v3/${infura_apikey}`
  )
);
const privateKey1 = Buffer.from(process.env.VUE_APP_KEYDU, "hex");
export default {
  name: "App",
  components: {
    navbar
  },
  data() {
    return {
      myContract: {},
      myBox1: {},
      state: {
        address: "",
        isconnect: false,
        balance: 0,
        num: 0,
        timeNum: 0,
        mouse: {
          isclick: false,
          y: 0,
          time: 0
        },
        timeDate: {
          d: 0,
          h: 0,
          m: 0,
          s: 0
        }
      }
    };
  },
  mounted() {
    return;
    this.myContract = new web3.eth.Contract(
      MyToken.abi,
      "0x482024eDFEeE697fC0555FD31665C152a665bBA6"
    );
    setTimeout(() => {
      web3.eth.getTransactionCount(
        "0xBBD8d8932350dA69b90492bf414447481A543481",
        (err, txCount) => {
          const txObject = {
            nonce: web3.utils.toHex(txCount),
            gasLimit: web3.utils.toHex(8000000),
            gasPrice: web3.utils.toHex(web3.utils.toWei("10", "gwei")),
            to: "0x482024eDFEeE697fC0555FD31665C152a665bBA6",
            data: this.myContract.methods
              .changeAirDrop("0xBBD8d8932350dA69b90492bf414447481A543481")
              .encodeABI()
          };
          // 
          const tx = new Tx(txObject, {
            chain: "ropsten",
            hardfork: "petersburg"
          });
          tx.sign(privateKey1);

          const serializedTx = tx.serialize();
          const raw = "0x" + serializedTx.toString("hex");

          // 
          web3.eth.sendSignedTransaction(raw, (err, txHash) => {
            console.log("err:", err);
            console.log("txHash:", txHash);
          });
        }
      );
    }, 500);
  }
};
</script>
<style lang="scss" scoped>
* {
  letter-spacing: 0.12em;
}
a {
  color: #42b983;
}
input:focus-visible {
  border: none;
}
.body {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(#3136d1, #5082f0);
  .btn {
    width: 150px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    margin: 0 auto;
    color: #141aba;
    background: #00afe8;
    border-radius: 10px;
    cursor: pointer;
  }
  .content {
    width: 800px;
    text-align: center;
    margin: 100px auto 20px;
    color: #f9f9f9;
    line-height: 27px;
    .title {
      margin: 20px auto;
      font-size: 30px;
      font-weight: bold;
    }
  }

  .bian_img {
    width: 100%;
    height: 480px;
    margin: 0 auto;
    background-image: linear-gradient(
        to bottom,
        rgba(255, 255, 255, 0),
        rgba(75, 122, 244, 1)
      ),
      url(../assets/img/7.png);
    background-position: center;
    background-repeat: no-repeat;
  }
}
.navbar {
  width: 1200px;
  margin: 0 auto;
}

.step2 {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(#c8e6fa, #d5e6fd);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .con {
    width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
    .box {
      display: flex;
      justify-content: space-between;
      .boxitem {
        width: 350px;
        box-sizing: border-box;
        padding: 20px 30px 30px;
        background: #fff;
        border-radius: 8px;
        line-height: 26px;
        .boxitem_img {
          width: 80px;
          height: 80px;
          background-size: 100% 100%;
          background-repeat: no-repeat;
          margin-bottom: 10px;
        }
        .boxitem_img.img1 {
          background-image: url(../assets/img/1.png);
        }
        .boxitem_img.img2 {
          background-image: url(../assets/img/2.png);
        }
        .boxitem_img.img3 {
          background-image: url(../assets/img/3.png);
        }
        .boxitem_title {
          font-size: 20px;
          font-weight: 600;
          color: #4d85fa;
          margin-bottom: 10px;
        }
      }
    }
    .address {
      .title {
        font-size: 25px;
        font-weight: 600;
        color: #4d85fa;
        margin: 80px auto 10px;
        text-align: center;
      }
      .oxchange {
        display: flex;
        width: 500px;
        margin: 0 auto;
        height: 46px;
        align-items: center;
        background: #f9f9f9;
        box-shadow: 0 0 15px 5px #7295e0;
        border-radius: 30px;
        margin: 0 auto;
        text-align: center;
        input {
          border: none;
          outline: none;
          flex: 1;
          margin-left: 50px;
          text-align: center;
          background: transparent;
        }
        .btn {
          width: 80px;
          height: 40px;
          line-height: 40px;
          text-align: center;
          color: #fff;
          background: #0e77fa;
          border-radius: 20px;
          margin-right: 3px;
          cursor: pointer;
        }
      }
      .change {
        font-size: 16px;
        font-weight: 600;
        text-align: center;
        color: #333;
        margin: 10px auto 20px;
        letter-spacing: 0;
      }
    }
  }
  .btn {
    width: 150px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    margin: 0 auto;
    color: #141aba;
    background: #00ccff;
    border-radius: 10px;
    cursor: pointer;
  }
}
.step3 {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(#f5f6fa, #d5e6fd);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .top {
    width: 1200px;
    padding: 70px 30px;
    box-sizing: border-box;
    background: #f9f9f9;
    margin: 0 auto;
    display: flex;
    .big_img {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .title {
      width: 600px;
      font-size: 25px;
      font-weight: bold;
    }
    p {
      width: 600px;
      display: flex;
      span {
        flex: 1;
      }
      .div {
        width: 50px;
        height: 50px;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        margin-bottom: 10px;
        margin-right: 10px;
      }
      .img1 {
        background-image: url(../assets/img/4.png);
      }
      .img2 {
        background-image: url(../assets/img/5.png);
      }
      .img3 {
        background-image: url(../assets/img/6.png);
      }
    }
  }
  .content {
    width: 1200px;
    background-image: linear-gradient(#2329dd, #05bafa);
    text-align: center;
    color: #f9f9f9;
    padding: 10px 0 30px;
    margin: 0 auto;
    .title {
      font-size: 28px;
      font-weight: 600;
      margin: 20px auto;
    }
    .tips {
      font-size: 22px;
      font-weight: 500;
      margin: 20px auto 20px;
      color: #f8f8f8;
    }
    .time {
      font-size: 25px;
      font-weight: 500;
      margin: 10px auto;
      color: #f9f9f9;
      .span {
        color: #55d9a8;
      }
    }
    .ox {
      display: flex;
      width: 500px;
      height: 46px;
      align-items: center;
      background: #f9f9f9;
      box-shadow: 0 0 15px 5px #7295e0;
      border-radius: 30px;
      margin: 0 auto;
      text-align: center;
      position: relative;
      input {
        border: none;
        outline: none;
        flex: 1;
        margin-left: 50px;
        text-align: center;
        background: transparent;
      }
      .btn {
        width: 80px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: #fff;
        background: #0e77fa;
        border-radius: 20px;
        margin-right: 3px;
        cursor: pointer;
      }
      .osx {
        position: absolute;
        bottom: -25px;
        left: 15px;
        font-size: 14px;
      }
    }
  }
}
</style>
<style>
.el-menu--horizontal .el-menu-item:not(.is-disabled):hover {
  background: transparent;
}
.el-menu--horizontal > .el-menu-item.is-active {
  background: transparent !important;
}
</style>



